<script lang="ts">
  import { flip } from "svelte/animate";
  import { fly } from "svelte/transition";

  import Toast from "./Toast.svelte";

  interface ToastSettings {
    success?: string | ((result: any) => string);
    error?: string | ((result: any) => string);
    loading?: string;
    minWidth?: number;
    duration?: number;
  }

  interface ToastData {
    settings: ToastSettings;
    promise?: Promise<any>;
    id: string;
  }

  let toasts: ToastData[] = [];

  const getDefaultToastSettings = (toastSettings: ToastSettings) => {
    const duration = toastSettings.duration || 3000;
    return { duration, ...toastSettings };
  };

  const addToast = (toastSettings: ToastSettings, promise: Promise<any>) => {
    toasts = [
      ...toasts,
      {
        settings: getDefaultToastSettings(toastSettings),
        promise: promise,
        id: generateRandomId(),
      },
    ];
  };

  const removeToast = (event: CustomEvent<string>) => {
    toasts = toasts.filter((toast) => toast.id != event.detail);
  };

  export const success = (text, options) => {
    addToast({ success: text, ...options }, Promise.resolve());
  };

  export const error = (text, options) => {
    addToast({ error: text, ...options }, Promise.reject());
  };

  export const promise = (promise: Promise<any>, settings: ToastSettings) => {
    addToast(settings, promise);
  };

  const generateRandomId = () => {
    return Math.random().toString(36).substr(2, 9);
  };
</script>
<div class="bg-red-500 text-3xl">
hola
</div>
<div
  style="z-index: 100000"
  class="toaster fixed top-0 left-0 w-full overflow-hidden pb-24 flex items-center justify-center flex-col gap-2 pt-2 pointer-events-none"
>
  {#each toasts as toast (toast.id)}
    <div animate:flip in:fly={{ y: 20 }} out:fly={{ y: -20 }}>
      <Toast
        on:death={removeToast}
        settings={toast.settings}
        promise={toast.promise}
        id={toast.id}
      />
    </div>
  {/each}
</div>

<style global lang="postcss">
  /* purgecss start ignore */
  @tailwind utilities;
  /* purgecss end ignore */

</style>