<script lang="ts">
  import { onMount } from "svelte";
  import { createEventDispatcher } from "svelte";
  import Loader from "./icon/Loader.svelte";
  import CheckmarkIcon from "./icon/CheckmarkIcon.svelte";
  import ErrorIcon from "./icon/ErrorIcon.svelte";

  const dispatch = createEventDispatcher();

  interface ToastSettings {
    success?: string | ((result: any) => string);
    error?: string | ((result: any) => string);
    loading?: string;
    minWidth?: number;
    duration?: number;
  }

  export let settings: ToastSettings = {
    success: "Success",
    error: "Error",
    loading: "Loading",
    minWidth: 200,
    duration: 2000,
  };

  export let id: string = "";

  const getResultOrDefault = (
    property: string | ((result: any) => string),
    result: any,
    defaultValue: string
  ) => {
    if (property instanceof Function) {
      return property(result);
    }

    if (property) return property;

    if (result && typeof result === "string") return result;

    return savedResult || defaultValue;
  };

  const getErrorMessage = (promiseResult: any) => {
    return getResultOrDefault(settings.error, promiseResult, "Error");
  };

  const getSuccessMessage = (promiseResult: any) => {
    return getResultOrDefault(settings.success, promiseResult, "Success");
  };

  let savedResult;

  const scheduleDeath = (result) => {
    savedResult = result;
    setTimeout(() => {
      dispatch("death", id);
    }, settings.duration);
  };

  onMount(() => {
    promise.then(scheduleDeath).catch(scheduleDeath);
  });

  export let promise: Promise<any>;
</script>

<div
  style="min-width: {settings.minWidth}px;"
  class="shadow-md rounded px-4 py-2 bg-white flex gap-4 items-center justify-start pointer-events-auto"
>
  {#await promise}
    <Loader /> <span>{settings.loading}</span>
  {:then result}
    <CheckmarkIcon /> <span>{getSuccessMessage(result)}</span>
  {:catch error}
    <ErrorIcon /> <span>{getErrorMessage(error)}</span>
  {/await}
</div>
